@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100..900&display=swap');

html, body{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    background: rgb(30, 102, 37);
    background: radial-gradient(circle, rgba(30, 102, 37) 5%,
                                        rgba(31,31,32,1) 100%);
}

.navbar-logo{
    font-size: 40px;
    color: white;
}

.navbar-page{
    color: #daab18;
    font-size: 18px;
    cursor: pointer;
    padding: 12px;
    border-radius: 10px;
}

.navbar-page:hover{
    background-color: rgba(150, 95, 18, 0.2);
}


a.btn-add-discord {
    background-color: white;
    padding: 14px;
    border-radius: 10px;
    color: #1c5f23;
    text-decoration: none;
    border: 1px outset #55d35f;
}

.btn-ver-caracteristicas{
    background-color: gray;
    padding: 14px;
    border-radius: 10px;
    border: 1px outset white;
    color: white;
}

.center-text{
    color: white;
    font-size: 35px;
}

.paragraph-center{
    color:#729e7e;
    font-size: 18px;
}

.btn-link{
    color: inherit;
    text-decoration: none;

}

@keyframes glowExpand {
  0% {
    box-shadow: 0 0 0 rgba(255, 255, 255, 0);
    text-shadow: 0 0 0 rgba(255, 255, 255, 0);
  }
  30% {
    box-shadow: 0 0 4px rgba(255, 255, 255, 0.3);
    text-shadow: 0 0 4px rgba(255, 255, 255, 0.3);
  }
  60% {
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.6);
  }
  100% {
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.9);
    text-shadow: 0 0 12px rgba(255, 255, 255, 0.9);
  }
}

.glow-animate {
  transition: all 0.4s ease;
}

.group:hover .glow-animate {
  animation: glowExpand 0.4s forwards;
}